import React, {useState, useEffect} from 'react'
import arrowRight from './Icons/arrowRight.svg'

export default function Password({updateAccess}) {

    const [val,
        setVal] = useState("")

    const [display,
        setDisplay] = useState("flex")

    const mdp = "Lacoste2021!"

    function handleChange(e) {
        setVal(e.target.value)
    }

    function handleSubmit(e) {
        e.preventDefault()
        if (val === mdp) {
            updateAccess(true);
            setDisplay('none');
        }
    }

    return (
        <div style={{
            display: display
        }} className="password z10">
            <div className="col-8 col-m-13 col-s-23 flex-col align-start justify-center">
                <h1 className="text-l text-align-center text-custom medium-light mgbt-text-30">Sign-in to see content</h1>
                <form className="flex align-center justify-center" onSubmit={handleSubmit}>
                    <input
                        type="password"
                        value={val}
                        onChange={handleChange}
                        placeholder="Password"/>
                    <button type="submit"><img className="icon" src={arrowRight}/></button>
                </form>
            </div>
        </div>
    )
}
